'use strict';

var React = require('react-native');
var Button = require('apsl-react-native-button');
var {
  View,
  ScrollView,
  Text,
  Image,
  TouchableOpacity,
  Platform,
  TextInput,
} = React;
var auth = require('../services/auth.service');
var userService = require('../services/user.service');
var listings = require('../services/listing.service');
var localStorage = require('../services/localStorage.service');

var styles = Platform.OS === 'android' ?
  require('../styles/baseStylesAndroid') : require('../styles/baseStylesIOS');

var LogIn = React.createClass({
  getInitialState: function(){
    return {
      loggedIn: null,
      email: null,
      password: null,
    };
  },
  componentDidMount: function(){
    localStorage.get('email')
      .then((email) => {
        if (email) {
          this.setState({
            email
          });
        }
      });
    userService.get()
      .then((user) => {
        this.setState({
          loggedIn: !!user.id
        });
      });
  },
  login: function() {
    return auth.login(this.state.email, this.state.password)
      .then(() => {
        localStorage.set('email', this.state.email);
        this.setState({
          loggedIn: true
        });
      })
      .catch((err) => {
        alert(err);
      });
  },
  logout: function() {
    return auth.logout()
      .then(() => {
        this.setState({
          loggedIn: false
        });
      })
      .catch((err) => {
        alert(err);
      });
  },
  test: function() {
    var vin = 'JM1NA3533R0501923'; //andrewj.crowell@gmail.com's car
    return localStorage.wip.listing.get()
      .then((listing) => {
        return localStorage.wip.listing.addImage('frontCorner', listing.frontCornerImageUri);
        //return listings.uploadImage(vin, {category:'exterior'}, listing.frontCornerImageUri);
      })
      .then((res) => {
        alert(JSON.stringify(res));
      });
  },
  clear: function() {
    return localStorage.clear()
      .then(() => {
        alert('DONE!');
      });
  },
  render: function() {
    var component = this;
    return (
      <ScrollView style={styles.container}>
        <Text style={styles.titleText}>TRED SUPPORT</Text>
        { component.state.loggedIn ?
          <Button onPress={component.logout} style={[styles.actionButton, {marginHorizontal:15}]} textStyle={styles.actionButtonText}>
            LOGOUT
          </Button>
        :
          <View style={styles.formField}>
            <Text style={[styles.label]}>Email</Text>
            <TextInput
              style={[styles.textInput]}
              onChangeText={(email) => this.setState({email})}
              value={this.state.email}
              keyboardType="email-address"
              autoCapitalize='none'
              autoCorrect={false} />
            <Text style={[styles.label]}>Password</Text>
            <TextInput
              style={[styles.textInput]}
              onChangeText={(password) => this.setState({password})}
              value={this.state.password}
              secureTextEntry={true}
              autoCapitalize='none'
              autoCorrect={false} />
            <Button onPress={component.login} style={[styles.actionButton, {marginHorizontal:15}]} textStyle={styles.actionButtonText}>
              LOGIN
            </Button>
          </View>
        }
        <Button onPress={component.test} style={[styles.actionButton, {marginHorizontal:15}]} textStyle={styles.actionButtonText}>
          UPLOAD TEST
        </Button>
        <Button onPress={component.clear} style={[styles.actionButton, {marginHorizontal:15}]} textStyle={styles.actionButtonText}>
          CLEAR LOCAL STORAGE
        </Button>
      </ScrollView>
    );
  }
});

module.exports = LogIn;
