'use strict';

var React = require('react-native');
var {
  View,
  ScrollView,
  Text,
  Image,
  TouchableOpacity,
  Platform,
  TextInput,
} = React;
var Button = require('apsl-react-native-button');
var localStorage = require('../services/localStorage.service.js');
var KeyboardHandler = require('../components/KeyboardHandler.js');

var styles = Platform.OS === 'android' ?
  require('../styles/baseStylesAndroid') : require('../styles/baseStylesIOS');

var zips = ["98001","98002","98003","98004","98005","98006","98007","98008","98009","98011","98012","98015","98020","98021","98023","98024","98026","98027","98028","98029","98030","98031","98032","98033","98034","98036","98037","98038","98039","98040","98041","98043","98047","98052","98053","98054","98055","98056","98057","98058","98059","98062","98065","98068","98072","98073","98074","98075","98077","98083","98087","98101","98102","98103","98104","98105","98106","98107","98108","98109","98111","98112","98114","98115","98116","98117","98118","98119","98121","98122","98124","98125","98126","98131","98132","98133","98134","98136","98138","98144","98145","98146","98148","98154","98155","98158","98160","98161","98164","98166","98168","98171","98174","98177","98178","98188","98198","98199","98201","98203","98204","98208","98205","98258","98270","98275","98290","98296","98354","98372","98421","98422","98424"];

var SignUp = React.createClass({
  getInitialState: function(){
    return {
      zip: null,
    };
  },
  componentDidMount: function(){
    return localStorage.wip.user.get().then((user) => {
      this.setState({
        zip: user.zip
      });
    });
  },
  onHowItWorks: function(e) {  
    this.props.navigator.push({
      id: 'SellHIWRoot',
      name: 'Sell How It Works One',
    });    
  },
  onSupport: function(e) {  
    this.props.navigator.push({
      id: 'Support',
      name: 'Get Support',
    });    
  },
  validateZip: function(zip){
    if(typeof zip !== 'undefined' && zip && zip.length > 0 && zips.indexOf(zip) == -1) {
      return false;
    }
    return true;
  },
  onConfirm: function(e) {
    if(!this.state.zip){
      alert("Please enter your zip code to continue.");
      return;
    }
    if(this.validateZip(this.state.zip)){
      localStorage.wip.user.upsert({ zip: this.state.zip }).then(() => {
        this.props.navigator.push({
          id: 'PhotoVin',
          name: 'Photo Vin',
        });    
      });
    } else {
      alert("Ack! We aren't in your area yet, but we will be very very soon. Questions? Feel free to reach us at help@tred.com.");
    }
  },
  render: function() {
    return (
      <View style={[styles.signUpContainer, {flex:1}]}>
        <KeyboardHandler ref='kh' offset={30}>
          <View style={styles.centerContainer}>
            <View style={styles.innerContainer}>
              <Image 
                source={require('../../images/sellcar.png')}
                style={styles.signupImage} />
              <Image 
                source={require('../../images/logo.png')}
                style={styles.logo} />
              <Text style={[styles.pText, {fontSize:18}]}>Sell your car quickly for more money.</Text>
            </View>
          </View>
          <View style={[styles.formField, {marginTop: 10}]}>
             <Text style={[styles.label, {textAlign:'center'}]}>Enter Your Zip Code</Text>
            <TextInput
              ref="zip"
              enablesReturnKeyAutomatically={true}
              keyboardType="numeric"
              style={[styles.textInput, {textAlign:'center', fontSize:30, paddingHorizontal:5, height:52, lineHeight:30}]}
              onChangeText={(zip) => this.setState({zip})}
              value={this.state.zip} 
              onFocus={()=>this.refs['kh'].inputFocused(this,'zip')}
              maxLength={5} />
          </View>
          <View style={styles.centerContainer}>
            <Button style={[styles.actionButton]}
              textStyle={styles.actionButtonText}
              onPress={this.onConfirm}>
                SELL YOUR CAR
            </Button>
            <Text style={[styles.actionLink, {flex:1, alignItems:'stretch', textAlign:'center'}]} onPress={this.onHowItWorks}>How does TRED work?</Text>
            <Text style={[styles.actionLink, {flex:1, alignItems:'stretch', textAlign:'center', marginTop:10}]} onPress={this.onSupport}>Get Support / Chat Online</Text>
          </View>
        </KeyboardHandler>
      </View>
    );
  }
});

module.exports = SignUp;
