'use strict';

var React = require('react-native');
var {
  View,
  ScrollView,
  Text,
  Image,
  TouchableOpacity,
  Platform,
  TextInput,
  SegmentedControlIOS,
} = React;

var Button = require('apsl-react-native-button');
var KeyboardHandler = require('../components/KeyboardHandler.js');
var LocalStorage = require('../services/localStorage.service.js');

var styles = Platform.OS === 'android' ?
  require('../styles/baseStylesAndroid') : require('../styles/baseStylesIOS');

var LoanInfo = React.createClass({
  getInitialState: function(){
    return {
      has_loan: null,
      loan_amount: null,
      bank_name: null,
    };
  },
  componentDidMount: function(){
    var component = this;
    LocalStorage.wip.listing.get().then(function(listing){
      component.setState({
        has_loan: typeof listing.has_loan !== 'undefined' && listing.has_loan !== null ? listing.has_loan : false,
        loan_amount: listing.loan_amount,
        bank_name: listing.bank_name,
      });
    });
  },
  onChangeLoanAmount: function(value){
    value = value.toString();
    value = value.replace(/[^0-9]/g, '');
    var num = value.replace(/,/gi, '');
    var num2 = num.replace(/\d(?=(?:\d{3})+$)/g, '$&,');
    this.setState({
      loan_amount: num2
    });
  },
  onConfirm: function(){
    var component = this;
    LocalStorage.wip.listing.update({ 
        has_loan: this.state.has_loan,
        loan_amount: this.state.loan_amount,
        bank_name: this.state.bank_name,
    }).then(function(){
      component.props.navigator.push({
        id: 'AboutYou',
        name: 'About You',
      });    
    });
  },
  render: function() {
    return (
      <View style={{flex:.9}}>
        <KeyboardHandler ref='kh' offset={30}>
          <View>
            <Text style={styles.cameraTitle}>LOAN INFORMATION</Text>
            <Text style={styles.pText}>We&apos;d like to help you repay your loan.</Text>
            
            <View style={styles.segment}>
              <View style={styles.segmentLabel}>
                <Text style={styles.segmentText}>
                 Is there a loan on the vehicle?
                </Text>
              </View>
              <View style={styles.segmentControl}>
                <SegmentedControlIOS 
                  tintColor='#ffffff'
                  values={['No', 'Yes']}
                  selectedIndex={this.state.has_loan ? 1 : 0}
                  onValueChange={(value) => this.setState({has_loan: value === "Yes" ? true : false})} />
              </View>
            </View>

            {this.state.has_loan ?
              <View>
                <View style={styles.formField}>
                  <Text style={[styles.label]}>How much is owed on the loan?</Text>
                  <TextInput
                    ref="loan_amount"
                    keyboardType='numeric'
                    style={[styles.textInput]}
                    onChangeText={this.onChangeLoanAmount}
                    value={this.state.loan_amount}
                    onFocus={()=>this.refs['kh'].inputFocused(this,'loan_amount')} />
                </View>
                <View style={styles.formField}>
                  <Text style={[styles.label]}>What is the name of the bank carrying your loan?</Text>
                  <TextInput
                    ref="bank_name"
                    style={[styles.textInput]}
                    onChangeText={(bank_name) => this.setState({bank_name})}
                    value={this.state.bank_name} 
                    onFocus={()=>this.refs['kh'].inputFocused(this,'bank_name')} />
                </View>
              </View>
            :
              null
            }
          </View>
        </KeyboardHandler>
        <View style={{flex:.1}}>
          <View style={styles.buttonsBottom}>
            <View style={styles.formField}>
              <Button onPress={this.onConfirm} style={[styles.actionButton, {marginHorizontal:15}]} textStyle={styles.actionButtonText}>
                CONTINUE
              </Button>
            </View>
          </View>
        </View>
      </View>
    );
  },
});

module.exports = LoanInfo;
