'use strict';

var React = require('react-native');
var {
  View,
  Text,
  Image,
  TouchableOpacity,
  Platform,
} = React;
var Camera = require('../components/Camera');
var localStorage = require('../services/localStorage.service');

var styles = Platform.OS === 'android' ?
  require('../styles/baseStylesAndroid') : require('../styles/baseStylesIOS');

var PhotoFrontDriverCorner = React.createClass({
  getInitialState: function() {
    return {
      lastCaptureUri: null,
      loading: true
    };
  },
  componentDidMount: function(){
    return localStorage.wip.listing.getImages()
      .then((images) => {
        this.setState({
          lastCaptureUri: images && images.frontDriverCorner,
          loading: false,
        });
      });
  },
  onConfirm: function(data) {
    return localStorage.wip.listing.addImage('frontDriverCorner', data.imageUri)
      .then(() => {
        this.props.navigator.push({
          id: 'PhotoDriverSide',
          name: 'Photo Driver Side',
        });
      });
  },
  render: function() {
    return (
      <View style={styles.cameraScene}>
        { this.state.loading ? null : <Camera
          title="Front Driver Corner"
          instructions="Take a photo of the front driver corner of the vehicle. Try to align it as best you can to the guide image above."
          overlayImage={require('../../images/overlays/photo-front-driver-corner.png')}
          vinRead={false}
          onConfirm={this.onConfirm}
          lastCaptureUri={this.state.lastCaptureUri}>
        </Camera>}
      </View>
    );
  }
});

module.exports = PhotoFrontDriverCorner;
