'use strict';

var React = require('react-native');
var {
  View,
  Text,
  Image,
  TouchableOpacity,
  Platform,
} = React;
var Camera = require('../components/Camera');
var localStorage = require('../services/localStorage.service');

var styles = Platform.OS === 'android' ?
  require('../styles/baseStylesAndroid') : require('../styles/baseStylesIOS');

var PhotoDashboard = React.createClass({
  getInitialState: function() {
    return {
      lastCaptureUri: null,
      loading: true
    };
  },
  componentDidMount: function(){
    return localStorage.wip.listing.getImages()
      .then((images) => {
        this.setState({
          lastCaptureUri: images && images.dashboard,
          loading: false,
        });
      });
  },
  onConfirm: function(data) {
    return localStorage.wip.listing.addImage('dashboard', data.imageUri)
      .then(() => {
        this.props.navigator.push({
          id: 'PhotoFrontInterior',
          name: 'Photo Front Interior',
        });
      });
  },
  render: function() {
    return (
      <View style={styles.cameraScene}>
        { this.state.loading ? null : <Camera
          title="Dashboard w/ Power On"
          instructions="Take a photo of the dashboard with the power on. Try to align it as best you can to the guide image above."
          overlayImage={require('../../images/overlays/photo-dashboard.png')}
          vinRead={false}
          onConfirm={this.onConfirm}
          lastCaptureUri={this.state.lastCaptureUri}>
        </Camera>}
      </View>
    );
  }
});

module.exports = PhotoDashboard;
