'use strict';

var React = require('react-native');
var {
  View,
  ScrollView,
  Text,
  Image,
  TouchableOpacity,
  Platform,
  TextInput,
  SegmentedControlIOS,
} = React;

var Button = require('apsl-react-native-button');
var KeyboardHandler = require('../components/KeyboardHandler.js');
var LocalStorage = require('../services/localStorage.service.js');

var styles = Platform.OS === 'android' ?
  require('../styles/baseStylesAndroid') : require('../styles/baseStylesIOS');

var PriceGuidance = React.createClass({
  getInitialState: function(){
    return {
      starting_list_price: null,
    };
  },
  componentDidMount: function(){
    var component = this;
    LocalStorage.wip.listing.get().then(function(listing){
      component.setState({
        starting_list_price: listing.starting_list_price
      });
    });
  },
  onChangeListPrice: function(value){
    value = value.toString();
    value = value.replace(/[^0-9]/g, '');
    var num = value.replace(/,/gi, '');
    var num2 = num.replace(/\d(?=(?:\d{3})+$)/g, '$&,');
    this.setState({
      starting_list_price: num2
    });
  },
  onConfirm: function(){
    var component = this;
    LocalStorage.wip.listing.update({ 
      starting_list_price: this.state.starting_list_price 
    }).then(function(){
      component.props.navigator.push({
        id: 'Finish',
        name: 'Finish',
      });    
    });
  },
  render: function() {
    console.log(this.state.starting_list_price);
    return ( 
      <View style={{flex:.9}}>
        <KeyboardHandler ref='kh' offset={30}>
          <View>
            <Text style={styles.cameraTitle}>PRICING GUIDANCE</Text>
            <Text style={styles.pText}>We use KBB and other statistics to determine a suggested starting list price for your vehicle.</Text>
            
            <View style={[styles.table, styles.tableGreen]}>
              <View style={styles.tableCol80}>
                <Text style={[styles.tableText, {color:'white'}]}>Suggested TRED List Price</Text>
              </View>
              <View style={styles.tableCol20}>
                <Text style={[styles.tableText, {fontWeight:'bold', color:'white'}]}>$32,130</Text>
              </View>
            </View>
            <View style={[styles.table, styles.tableBlue]}>
              <View style={styles.tableCol80}>
                <Text style={[styles.tableText, {color:'white'}]}>Craigslist Private Party Price</Text>
              </View>
              <View style={styles.tableCol20}>
                <Text style={[styles.tableText, {fontWeight:'bold', color:'white'}]}>$29,750</Text>
              </View>
            </View>
            <View style={[styles.table, styles.tablePurple]}>
              <View style={styles.tableCol80}>
                <Text style={[styles.tableText, {color:'white'}]}>Dealer Trade In Price</Text>
              </View>
              <View style={styles.tableCol20}>
                <Text style={[styles.tableText, {fontWeight:'bold', color:'white'}]}>$26,800</Text>
              </View>
            </View>

            <View style={[styles.table, styles.tableTop]}>
              <View style={styles.tableCol80}>
                <Text style={[styles.tableText]}>Estimated Time To Sell</Text>
              </View>
              <View style={styles.tableCol20}>
                <Text style={[styles.tableText, {fontWeight:'bold'}]}>16 days</Text>
              </View>
            </View>
            <View style={[styles.table, styles.tableBottom]}>
              <View style={styles.tableCol80}>
                <Text style={[styles.tableText]}>Estimated Num. of Test Drives</Text>
              </View>
              <View style={styles.tableCol20}>
                <Text style={[styles.tableText, {fontWeight:'bold'}]}>1.2</Text>
              </View>
            </View>
            <View style={[styles.formField, {marginTop:15}]}>
              <Text style={[styles.label]}>Choose your starting list price</Text>
              <TextInput
                ref="starting_list_price"
                placeholder="$32,130"
                keyboardType='numeric'
                style={[styles.textInput]}
                onChangeText={this.onChangeListPrice}
                value={this.state.starting_list_price}
                onFocus={()=>this.refs['kh'].inputFocused(this,'starting_list_price')} />
            </View>
          </View>
        </KeyboardHandler>
        <View style={{flex:.1}}>
          <View style={styles.buttonsBottom}>
            <View style={styles.formField}>
              <Button onPress={this.onConfirm} style={[styles.actionButton, {marginHorizontal:15}]} textStyle={styles.actionButtonText}>
                CONFIRM
              </Button>
            </View>
          </View>
        </View>
      </View>
    );
  },
});

module.exports = PriceGuidance;
