'use strict';

var React = require('react-native');
var Button = require('apsl-react-native-button');
var RCTCamera = require('react-native-camera');
var {
  View,
  Text,
  Image,
  TouchableHighlight,
  Platform,
  Image,
  Modal,
  TextInput,
  } = React;
var edmunds = require('../services/edmunds.service');
var styles = Platform.OS === 'android' ?
  require('../styles/baseStylesAndroid') : require('../styles/baseStylesIOS');
var FMPicker = require('../components/BottomPicker');

var Camera = React.createClass({
  getInitialState() {
    return {
      capturing: true,
      processingBarcode: false,
      lastCaptureUri: null,
      fetchingVinInfo: false,
      errorFetchingVin: null,
      vinInfo: null,
      modalVisible:false,
      makes: null,
      models: null,
      trims: null
    };
  },
  getDefaultProps() {
    return {
      title: null,
      instructions: null,
      overlayImage: null,
      vinInfo: null,
      lastCaptureUri: null,
    }
  },
  componentWillMount() {
    if (this.props.lastCaptureUri) {
      // This picture was already taken, show the review panel
      this.setState({
        capturing: false,
        lastCaptureUri: this.props.lastCaptureUri,
        vinInfo: this.props.vinInfo,
      });
    }
  },
  onBarCodeRead(e) {
    var self = this;
    if (this.props.vinRead && !this.state.processingBarcode) {
      self.setState({
        fetchingVinInfo: true,
        processingBarcode: true,
      });
      this.refs.cam.capture(function (err, data) {
        self.setState({
          capturing: false,
          lastCaptureUri: data,
        });
        var vin = e.data;
        if (e.data.length === 18) {
          // sometimes the barcode includes a leading character not part of the VIN
          vin = e.data.slice(1);
        }
        return edmunds.vin(vin)
          .then(function (res) {
            self.setState({
              fetchingVinInfo: false,
              errorFetchingVin: null,
              vinInfo: res,
            });
          })
          .catch(function (err) {
            self.setState({
              fetchingVinInfo: false,
              errorFetchingVin: err,
              vinInfo: null,
            });
          });
      });
    }
  },
  retake() {
    this.setState({
      capturing: true,
      processingBarcode: false,
      errorFetchingVin: null,
      lastCaptureUri: null,
      vinInfo: null,
    });
  },
  takePictureiOS() {
    var self = this;
    this.refs.cam.capture(function(err, data) {
      self.setState({
        capturing: false,
        lastCaptureUri: data
      });
    });
  },
  takePictureAndroid() {
    var self = this;
    this.refs.cam.capture(function(data) {
      self.setState({
        capturing: false,
        lastCaptureUri: 'data:image/jpeg;base64,' + data
      });
    });
  },
  manualEntry(){
    this.setState({
      modalVisible: true
    });
  },
  onHideManualEntry(){
    this.setState({
      modalVisible: false
    });
  },
  onSubmitManualEntry(){
    //TODO: Submit manual entry of vehicle
    this.setState({
      modalVisible: false
    });
  },
  onSelectYear(){
    this.refs.yearPicker.show();    
  },
  render() {
    if (this.state.capturing) {
      return this.renderCamera();
    } else {
      return this.renderReview();
    }
  },
  renderCamera() {
    var component = this;
    var years = [];
    var year = new Date().getFullYear() + 1;
    for(var i = 0; i < 20; i++) {
      years.push((year - i).toString());
    }

    return Platform.OS === 'android' ?
      (
        <View style={styles.camera}>
          <RCTCamera ref="cam" style={styles.camera} type={RCTCamera.constants.Type.back} captureTarget={RCTCamera.constants.CaptureTarget.memory}></RCTCamera>
          <Button style={[styles.captureButton, {marginHorizontal:30}]} textStyle={styles.actionButtonText} onPress={this.takePictureAndroid}>
            CAPTURE
          </Button>
        </View>
      )
          :
      (
        <View style={{flex:1}}>
          <View>
            <Text style={styles.cameraTitle}>{component.props.title}</Text>
          </View>
          <View style={styles.camera}>
            <RCTCamera style={styles.camera} ref="cam" onBarCodeRead={component.onBarCodeRead} type={RCTCamera.constants.Type.back} captureTarget={RCTCamera.constants.CaptureTarget.disk}>
              <Image 
                source={component.props.overlayImage}
                style={styles.overlayImage} />
            </RCTCamera>
          </View>
          <View style={{flex:1}}>
            { component.state.fetchingVinInfo ? <View>
                <Text style={[styles.cameraText, {fontSize:22}]}>Looking up your vehicle...please wait.</Text>
              </View>
            :
              <Text style={styles.cameraText}>{component.props.instructions}</Text>
            }
            { component.props.vinRead ?
              <View style={{flex:1}}>
                <Modal
                style={{flex:1}}
                  animated={true}
                  transparent={false}
                  visible={this.state.modalVisible}>
                  <View style={[styles.modalContainer]}>
                    <View style={[styles.modalInnerContainer, {flex:1, alignItems:'stretch'}]}>
                      <Text style={styles.modalTitle}>YOUR VEHICLE</Text>
                      <Text style={styles.pText}>Tell us which vehicle you are selling.</Text>
                      <View>
                        <Text style={[styles.label]}>Year</Text>
                        <View style={[styles.inputContainer]}>
                          <Text
                            style={styles.inputText}
                            onPress={()=>{
                              this.onSelectYear();
                            }}>
                            {this.state.year}
                          </Text>
                        </View>
                        <FMPicker ref='yearPicker' options={years}
                            onSubmit={(option)=>{
                                this.setState({year: option})
                            }} />
                      </View>
                      <View>
                        <Text style={[styles.label]}>Make</Text>
                        <View style={[styles.inputContainer]}>
                          <Text
                            style={styles.inputText}
                            onPress={()=>{
                              this.onSelectYear();
                            }}>
                            {this.state.make}
                          </Text>
                        </View>
                        <FMPicker ref='makePicker' options={years}
                            onSubmit={(option)=>{
                                this.setState({year: option})
                            }} />
                      </View>
                      <View>
                        <Text style={[styles.label]}>Model</Text>
                        <View style={[styles.inputContainer]}>
                          <Text
                            style={styles.inputText}
                            onPress={()=>{
                              this.onSelectYear();
                            }}>
                            {this.state.model}
                          </Text>
                        </View>
                        <FMPicker ref='modelPicker' options={years}
                            onSubmit={(option)=>{
                                this.setState({year: option})
                            }} />
                      </View>
                      <View>
                        <Text style={[styles.label]}>Trim</Text>
                        <View style={[styles.inputContainer]}>
                          <Text
                            style={styles.inputText}
                            onPress={()=>{
                              this.onSelectYear();
                            }}>
                            {this.state.trim}
                          </Text>
                        </View>
                        <FMPicker ref='trimPicker' options={years}
                            onSubmit={(option)=>{
                                this.setState({year: option})
                            }} />
                      </View>
                      <View style={styles.formField}>
                        <Text style={[styles.label]}>VIN</Text>
                        <TextInput
                          ref='vin'
                          style={[styles.textInput]}
                          onChangeText={(vin) => this.setState({vin})}
                          value={this.state.night_street} maxLength={17} />
                      </View>
                      <View style={styles.buttonsBottom}>
                        <View style={styles.formFieldHorizontal}>
                          <Button onPress={this.onHideManualEntry}  style={[styles.cancelButton, styles.left, {flex:1, marginLeft:15}]} textStyle={styles.cancelButtonText}>
                            CANCEL
                          </Button>
                          <Button style={[styles.actionButton, styles.right, {flex:1, marginRight:15}]} textStyle={styles.actionButtonText} onPress={this.onSubmitManualEntry}>
                            SUBMIT
                          </Button>
                        </View>
                      </View>
                    </View>
                  </View>
                </Modal>

                <View style={styles.buttonsBottom}>
                  <View style={styles.formField}>
                    <Button  onPress={this.manualEntry} style={[styles.actionButton, {marginHorizontal:15}]} textStyle={styles.actionButtonText}>
                      ENTER MANUALLY
                    </Button>
                  </View>
                </View>
              </View>
            :
              <View style={styles.buttonsBottom}>
                <View style={styles.formField}>
                  <Button onPress={component.takePictureiOS} style={[styles.actionButton, {marginHorizontal:15}]} textStyle={styles.actionButtonText}>
                    SHOOT
                  </Button>
                </View>
              </View>
            }
          </View>
        </View>
      );
  },
  renderReview() {
    var component = this;
    var vinInfo = component.state.vinInfo;
    return (
      <View style={{flex:1}}>
        <View>
          <Text style={styles.cameraTitle}>{component.props.title}</Text>
        </View>
        { vinInfo ? null :
          <View>
            <Image style={styles.capturedImage} source={{uri: this.state.lastCaptureUri}}/>
          </View>
        }
        { vinInfo ?
          <View>
            <Text style={[styles.cameraText, {marginTop:5}]}>Confirm this is your vehicle.</Text>
            <View style={[styles.table, styles.tableTop]}>
              <View style={styles.tableCol30}>
                <Text style={[styles.tableText, styles.tableHead]}>VEHICLE</Text>
              </View>
              <View style={styles.tableCol70}>
                <Text style={[styles.tableText]}>{(vinInfo.years && vinInfo.years.length > 0 ? vinInfo.years[0].year + ' ' : '') + (vinInfo.make ? vinInfo.make.name + ' ' : '') + (vinInfo.model ? vinInfo.model.name : '')}</Text>
              </View>
            </View>
            <View style={[styles.table]}>
              <View style={styles.tableCol30}>
                <Text style={[styles.tableText, styles.tableHead]}>TRIM</Text>
              </View>
              <View style={styles.tableCol70}>
                <Text style={[styles.tableText]}>{vinInfo.years && vinInfo.years.length > 0 && vinInfo.years[0].styles && vinInfo.years[0].styles.length > 0 && vinInfo.years[0].styles[0].name ? vinInfo.years[0].styles[0].name : 'N/A'}</Text>
              </View>
            </View>
            <View style={[styles.table, styles.tableBottom]}>
              <View style={styles.tableCol30}>
                <Text style={[styles.tableText, styles.tableHead]}>VIN</Text>
              </View>
              <View style={styles.tableCol70}>
                <Text style={[styles.tableText]}>{(vinInfo.vin ? vinInfo.vin  : '')}</Text>
              </View>
            </View>
          </View>
        :
          <View>
            <Text style={styles.cameraText}>Make sure the photo is centered and in focus. A good photo means your car will sell faster. Click accept to continue.</Text>
          </View>
        }
        <View style={styles.buttonsBottom}>
          <View style={styles.formFieldHorizontal}>
            <Button onPress={this.retake} style={[styles.secondaryButton, styles.left, {flex:1, marginLeft:15}]} textStyle={styles.actionButtonText}>
              { vinInfo ? 'CHANGE' : 'RESHOOT' }
            </Button>
            <Button style={[styles.actionButton, styles.right, {flex:1, marginRight:15}]} textStyle={styles.actionButtonText} onPress={component.props.onConfirm.bind(null, {vinInfo: component.state.vinInfo, imageUri: component.state.lastCaptureUri})}>
              { vinInfo ? 'CONFIRM' : 'ACCEPT' }
            </Button>
          </View>
        </View>
      </View>
    )
  }
});

module.exports = Camera;