var config = require('../../config');

function url(path) {
  return config.api_host + (config.api_port ? ':' + config.api_port : '') + path;
}

exports.post = function(path, data) {
  return fetch(url(path), {
    method: 'POST',
    headers: {
      'Accept': 'application/json',
      'Content-Type': data instanceof FormData ? 'multipart/form-data; boundary=----WebKitFormBoundary5X7VbApxbrBIahrM' : 'application/json'
    },
    body: data instanceof FormData ? data : JSON.stringify(data)
  }).then(function(res) {
    if (res.status !== 200) {
      throw new Error(res._bodyText);
    } else {
      return res.json().catch((e) => { return {ok: true}; });
    }
  }).catch(function(err) {
    console.log(err);
    throw err;
  });
};

exports.get = function(path) {
  return fetch(url(path), {
    method: 'GET',
    headers: {
      'Accept': 'application/json',
      'Content-Type': 'application/json'
    }
  }).then(function(res) {
    if (res.status !== 200) {
      throw new Error(res._bodyText);
    } else {
      return res.json().catch((e) => { return {ok: true}; });
    }
  }).catch(function(err) {
    console.log(err);
    throw err;
  });
};
