'use strict';

var React = require('react-native');
var {
  View,
  Text,
  Image,
  TouchableOpacity,
  Platform,
  Navigator,
} = React;

var styles = Platform.OS === 'android' ?
  require('./styles/baseStylesAndroid') : require('./styles/baseStylesIOS');
var PageControl = require('react-native-page-control');
var NavigationBarRouteMapper = require('./components/NavigationBarRouteMapper.js');
var SellHIWOne = require('./scenes/SellHIWOne');
var SellHIWTwo = require('./scenes/SellHIWTwo');
var SellHIWThree = require('./scenes/SellHIWThree');
var SellHIWFour = require('./scenes/SellHIWFour');

var step1Route = {id:"SellHIWOne", index:0};
var step2Route = {id:"SellHIWTwo", index:1};
var step3Route = {id:"SellHIWThree", index:2};
var step4Route = {id:"SellHIWFour", index:3};

var SellHIWRoot = React.createClass({
  getInitialState: function(){
    return {
      page:0,
    }
  },
  componentDidMount: function(){
    this.refs.nav.navigationContext.addListener('willfocus', (event) => {
      var route = event.data.route;
      this.setState({
        page: route.index
      });
    });
  },
  render: function(){
    return (
      <View style={{flex:1}}>
        <View style={{flex:.9}}>
          <Navigator
              ref="nav"
              initialRoute={step1Route}
              initialRouteStack={[step1Route, step2Route, step3Route, step4Route]}
              renderScene={this.renderScene}
              configureScene={() => { return Navigator.SceneConfigs.HorizontalSwipeJump}}
              navigationBar={
                <Navigator.NavigationBar style={[styles.navBar]} routeMapper={NavigationBarRouteMapper("Get Started", null,'SignUp', null, true)} />
              }
              sceneStyle={styles.scene} />
        </View>
        <View style={{flex:.1}}>
            <View style={styles.buttonsBottom}>
            <PageControl style={{position:'absolute', left:0, right:0, bottom:10}}
             numberOfPages={4}
             currentPage={this.state.page}
             hidesForSinglePage={true}
             pageIndicatorTintColor='#009DCD'
             currentPageIndicatorTintColor='white'
             indicatorStyle={{borderRadius: 5}} 
             currentIndicatorStyle={{borderRadius: 5}}
             indicatorSize={{width:8, height:8}} />
          </View>
        </View>
      </View>
    );
  },
  renderScene: function(route, navigator){
    var routeId = route.id;
    if (routeId === 'SellHIWOne') {
      return (
        <SellHIWOne navigator={navigator} />
      );
    }
    if (routeId === 'SellHIWTwo') {
      return (
        <SellHIWTwo navigator={navigator} />
      );
    }
    if (routeId === 'SellHIWThree') {
      return (
        <SellHIWThree navigator={navigator} />
      );
    }
    if (routeId === 'SellHIWFour') {
      return (
        <SellHIWFour navigator={navigator} />
      );
    }
    return this.noRoute(navigator);
  },
  noRoute: function(navigator) {
    return (
      <View style={{flex:1}}>
        <View style={{flex:1, alignItems: 'stretch', justifyContent:'center'}}>
          <TouchableOpacity style={styles.container} onPress={() => navigator.pop()}>
            <Text style={styles.titleText}> OOPS! Go Back </Text>
          </TouchableOpacity>
        </View>
      </View>
    );
  }
});

module.exports = SellHIWRoot;
