'use strict';

var React = require('react-native');
var {
  View,
  Text,
  Image,
  TouchableOpacity,
  Platform,
} = React;
var Button = require('apsl-react-native-button');
var Camera = require('../components/Camera');
var localStorage = require('../services/localStorage.service');

var styles = Platform.OS === 'android' ?
  require('../styles/baseStylesAndroid') : require('../styles/baseStylesIOS');

var PhotoVin = React.createClass({
  getInitialState: function() {
    return {
      lastCaptureUri: null,
      vinInfo: null,
      loading: true
    };
  },
  componentDidMount: function(){
    return localStorage.wip.listing.getImages()
      .then((images) => {
        return localStorage.wip.listing.get()
          .then((listing) => {
            this.setState({
              lastCaptureUri: images && images.vin,
              vinInfo: listing && listing.vinInfo,
              loading: false,
            });
          });
    });
  },
  onConfirm: function(data) {
    // data.vinInfo and data.imageUri
    return localStorage.wip.listing.upsert({
      vin: data.vinInfo.vin,
      year: data.vinInfo.years && data.vinInfo.years.length > 0 && data.vinInfo.years[0].year,
      make: data.vinInfo.make && data.vinInfo.make.name,
      model: data.vinInfo.model && data.vinInfo.model.name,
      trim: data.vinInfo.years && data.vinInfo.years.length > 0 && data.vinInfo.years[0].styles && data.vinInfo.years[0].styles.length > 0 && data.vinInfo.years[0].styles[0].name,
      style_id: data.vinInfo.years && data.vinInfo.years.length > 0 && data.vinInfo.years[0].styles && data.vinInfo.years[0].styles.length > 0 && data.vinInfo.years[0].styles[0].id,
      vinInfo: data.vinInfo, // store the whole object to make returning to this scene easier
    })
      .then(() => {
        return localStorage.wip.listing.addImage('vin', data.imageUri);
      })
      .then(() => {
        this.props.navigator.push({
          id: 'PhotoFrontDriverCorner',
          name: 'Photo Front Driver Corner',
        });
      });
  },
  onSkip: function(){
    this.props.navigator.push({
      id: 'PhotoFrontDriverCorner',
      name: 'Photo Front Driver Corner',
    });        
  },
  render: function() {
    return (
      <View style={styles.cameraScene}>
        { this.state.loading ? null : <Camera
          title="Driver Door Well / VIN Sticker"
          instructions="Inside the driver door well, point your camera at the VIN sticker making sure the full number and bar code is clearly visible. Try to align it to the guide image above."
          overlayImage={require('../../images/overlays/photo-vin.png')}
          vinRead={true}
          onConfirm={this.onConfirm}
          vinInfo={this.state.vinInfo}
          lastCaptureUri={this.state.lastCaptureUri}>
        </Camera>}
      </View>        
    );
  }
});

module.exports = PhotoVin;
