var default_options = {

};
var config;

switch (process.env.NODE_ENV || "development") {
  case "production":
    config = Object.assign(default_options, {
      env: "production",
      api_host: "https://www.tred.com"
    });
    break;
  case "development":
  default:
    config = Object.assign(default_options, {
      env: "development",
      api_host: "https://staging.tred.com",
      //api_port: 4000
    });
    break;
}

module.exports = config;
