'use strict';

var React = require('react-native');
var {
  View,
  ScrollView,
  Text,
  Platform,
  TextInput,
  SegmentedControlIOS,
} = React;
var Button = require('apsl-react-native-button');
var FMPicker = require('../components/BottomPicker');
var KeyboardHandler = require('../components/KeyboardHandler.js');
var LocalStorage = require('../services/localStorage.service');
var timeframeOptions = ['I have time, no rush.', "Within a week", "Within three week", "Within six week", "Within two months"];
var styles = Platform.OS === 'android' ?
  require('../styles/baseStylesAndroid') : require('../styles/baseStylesIOS');

var AboutYou = React.createClass({
  getInitialState: function(){
    return {
      registered_owner: null,
      first_name: null,
      last_name: null,
      email: null,
      phone: null,
      timeframe: 'I have time, no rush.',
      password: null,
      verify_password: null,
    };
  },
  componentDidMount: function(){
    var component = this;
    LocalStorage.wip.listing.get().then(function(listing){
      component.setState({
        registered_owner:typeof listing.registered_owner !== 'undefined' && listing.registered_owner !== null ? listing.registered_owner : true,
        first_name: listing.first_name,
        last_name: listing.last_name,
        email: listing.email,
        phone: listing.phone,
        timeframe: listing.timeframe,
        password: listing.password,
        verify_password: listing.verify_password,
      });
    });
  },
  onSelectTimeframe: function(){
    setTimeout(()=>{
      let scrollResponder=this.refs.kh.refs.scrollView.getScrollResponder();
      scrollResponder.scrollResponderScrollNativeHandleToKeyboard(
        React.findNodeHandle(this.refs.timeframe),
        300, //additionalOffset
        true
      );
    }, 200);
    this.refs.timeFramePicker.show();
  },
  onConfirm: function(){
    var component = this;
    LocalStorage.wip.listing.update({ 
      registered_owner: this.state.registered_owner,
      first_name: this.state.first_name,
      last_name: this.state.last_name,
      email: this.state.email,
      phone: this.state.phone,
      timeframe: this.state.timeframe,
      password: this.state.password,
      verify_password: this.state.verify_password,
    }).then(function(){
      component.props.navigator.push({
        id: 'PriceGuidance',
        name: 'Price Guidance',
      });    
    });
  },
  render: function() {
    console.log(this.state.registered_owner)
    return (
      <View style={{flex:.9}}>
        <KeyboardHandler ref='kh' offset={30}>
          <View>
            <Text style={styles.cameraTitle}>ABOUT YOU</Text>
            <Text style={styles.pText}>To expedite the sale of your vehicle, make it easy for us to contact you.</Text>
            
            <View style={styles.segment}>
              <View style={styles.segmentLabel}>
                <Text style={styles.segmentText}>
                 Are you the registered owner?
                </Text>
              </View>
              <View style={styles.segmentControl}>
                <SegmentedControlIOS 
                  tintColor='#ffffff'
                  values={['No', 'Yes']}
                  selectedIndex={this.state.registered_owner ? 1 : 0}
                  onValueChange={(value) => this.setState({registered_owner: value === "Yes" ? true : false})} />
              </View>
            </View>

            <View style={styles.formFieldHorizontal}>
              <Text style={[styles.label, styles.left]}>First Name</Text>
              <Text style={[styles.label, styles.right]}>Last Name</Text>
            </View>
            <View style={styles.formFieldHorizontal}>
              <TextInput
                ref="first_name"
                style={[styles.textInput, styles.left]}
                onChangeText={(first_name) => this.setState({first_name})}
                value={this.state.first_name}
                onFocus={()=>this.refs['kh'].inputFocused(this,'first_name')} />
              <TextInput
                ref="last_name"
                style={[styles.textInput, styles.right]}
                onChangeText={(last_name) => this.setState({last_name})}
                value={this.state.last_name}
                onFocus={()=>this.refs['kh'].inputFocused(this,'last_name')} />
            </View>

            <View>
              <View style={styles.formField}>
                <Text style={[styles.label]}>Email Address</Text>
                <TextInput
                  ref="email"
                  keyboardType="email-address"
                  style={[styles.textInput]}
                  onChangeText={(email) => this.setState({email})}
                  value={this.state.email}
                  onFocus={()=>this.refs['kh'].inputFocused(this,'email')} />
              </View>
              {/*
              <View style={styles.formField}>
                <Text style={[styles.label]}>Phone Number</Text>
                <TextInput
                  ref="phone"
                  style={[styles.textInput]}
                  onChangeText={(phone) => this.setState({phone})}
                  value={this.state.phone}
                  onFocus={()=>this.refs['kh'].inputFocused(this,'phone')} />                  
              </View>
              */}
            </View>

            <View>
              <View style={styles.formField}>
                <Text style={[styles.label]}>What is your selling timeframe?</Text>
                <View style={[styles.inputContainer]}>
                  <Text
                    ref="timeframe"
                    style={styles.inputText}
                    onPress={()=>{
                      this.onSelectTimeframe();
                    }} >
                      {this.state.timeframe}
                  </Text>
                </View>
                <FMPicker ref='timeFramePicker' options={timeframeOptions}
                    onSubmit={(option)=>{
                        this.setState({timeframe: option})
                    }} />
              </View>
            </View>

            <View style={styles.formFieldHorizontal}>
              <Text style={[styles.label, styles.left]}>Password</Text>
              <Text style={[styles.label, styles.right]}>Verify Password</Text>
            </View>
            <View style={styles.formFieldHorizontal}>
              <TextInput
                ref="password"
                secureTextEntry={true}
                style={[styles.textInput, styles.left]}
                onChangeText={(password) => this.setState({password})}
                value={this.state.password}
                onFocus={()=>this.refs['kh'].inputFocused(this,'password')}  />
              <TextInput
                ref="verify_password"
                secureTextEntry={true}
                style={[styles.textInput, styles.right]}
                onChangeText={(verify_password) => this.setState({verify_password})}
                value={this.state.verify_password}
                onFocus={()=>this.refs['kh'].inputFocused(this,'verify_password')}  />
            </View>
          </View>
        </KeyboardHandler>
        <View style={{flex:.1}}>
          <View style={styles.buttonsBottom}>
            <View style={styles.formField}>
              <Button onPress={this.onConfirm} style={[styles.actionButton, {marginHorizontal:15}]} textStyle={styles.actionButtonText}>
                CONTINUE
              </Button>
            </View>
          </View>
        </View>
      </View>
    );
  },
});

module.exports = AboutYou;
